import { GeolocationService } from "@/services/GeolocationService";
import { TOGGLE_GEOPOSITION } from "@/store/modules/settings";
import { getAuth, signOut } from "@firebase/auth";
import { defineComponent } from "@vue/runtime-core";

export default defineComponent({
  data() {
    const showMenu = false;
    return { showMenu };
  },
  methods: {
    onClickOutside(/* _e: MouseEvent, _el: Event */) {
      this.showMenu = false;
    },
    logout() {
      signOut(getAuth()); //TODO - Fare logout altrove?
      this.$router.push("login");
    },
    installApp() {
      if (this.installEv) {
        this.installEv.prompt();
      }
    },
    updateApp() {
      if (this.updateAvailable) {
        window.location.reload();
      }
    },
    toggleGeo() {
      this.$store.commit(TOGGLE_GEOPOSITION);
      if (this.geoOn) {
        try {
          GeolocationService.getInstance();
        } catch (error) {
          console.log(
            "Geolocalizzazione non avviata per il seguente errore: ",
            error
          );
          this.$store.commit(TOGGLE_GEOPOSITION, false);
        }
      } else {
        GeolocationService.getInstance().stopWatchPosition();
      }
    },
  },
  computed: {
    installEv() {
      return this.$store.state.appState.installEvent;
    },
    updateAvailable() {
      return this.$store.state.appState.updateAvailable;
    },
    version() {
      return this.$store.state.appState.version;
    },
    geoOn() {
      return this.$store.state.settings.geopositionEnabled;
    },
    /* pos() {
      return this.$store.state.session.position;
    }, */
  },
});
