import { GeopositionException } from "@/exceptions/GeopositionException";
import { key } from "@/store";
import { useStore } from "vuex";

export class GeolocationService {

    private static instance: GeolocationService;
    private navigator: Navigator;
    private watchPositionId: number | null = null;

    private constructor() {
        this.navigator = navigator;
    }

    static getInstance() {
        return this.instance || (this.instance = new this());
    }

    startWatchPosition(callback: PositionCallback, options: PositionOptions = { enableHighAccuracy: false, maximumAge: 60 * 1000 }) {
        const store = useStore(key);

        if (!store.state.settings.geopositionEnabled) {
            throw new GeopositionException('Geoposition disabled!');
        }

        this.stopWatchPosition();
        if ("geolocation" in this.navigator) {
            this.watchPositionId = this.navigator.geolocation.watchPosition(
                (pos) => {
                    // store.commit(UPDATE_POSITION, pos);
                    callback(pos);
                },
                (err) => {
                    if (this.watchPositionId) {
                        this.stopWatchPosition();
                    }
                    throw new GeopositionException('Error getting the position', err);
                }, options
            );
        } else {
            throw new GeopositionException('Geoposition not available');
        }
    }

    stopWatchPosition() {
        if (this.watchPositionId) {
            this.navigator.geolocation.clearWatch(this.watchPositionId);
        }
    }

}